<template>
  <div id="info">
    <div class="tableHeader">
      <p>发布时间</p>
      <p>发布平台</p>
      <p>公告标题</p>
      <p>创建原因</p>
      <p>公告发布</p>
      <p>公告内容</p>
    </div>
    <div class="content2">
      <order-list :list="listShow"></order-list>
      <div class="paginationZone" v-show="listShow.length !== 0">
        <a-pagination
          :total="listAll.length"
          :show-total="
            (total, range) => `${range[0]}-${range[1]} of ${total} items`
          "
          :page-size="pageSize"
          :default-current="1"
          v-model:current="currentPage"
        />
      </div>
      <empty-data-placeholder
        v-if="listShow.length === 0"
        description="暂无工单数据"
      ></empty-data-placeholder>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>

    <div class="bottom">
      <div class="button" @click="showAddDialog">添加公告</div>
    </div>

    <div class="add-toast" v-if="ifShowAdd">
      <add-info
        class="add-info"
        @hideDialog="hideAddDialog"
        @addInfo="addInfo"
      ></add-info>
    </div>
  </div>
</template>

<script>
import AddInfo from "./childComponents/addInfoProvider/AddInfo.vue";
import DeleteEnsureDialog2 from "../../../components/content/adminTools/deleEnsureDialog2/DeleteEnsureDialog2.vue";
import EditReasonDialog from "../../../components/content/adminTools/editReasonDialog/EditReasonDialog.vue";
import Scroll from "../../../components/common/scroll/Scroll.vue";
import OrderList from "./childComponents/orderList/OrderList.vue";
import EmptyDataPlaceholder from "../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue";
import { $getProviderInfo } from "@/pages/internal/network/adminTools/info";

export default {
  name: "e-InfoProvider",
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isStaff") === "true") {
      window.localStorage.setItem("currentIndex010001", 3);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010001");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  beforeRouteLeave(to, from, next) {
    // ...
    if (this.updateMark) {
      if (confirm("还有修改未提交，确定离开？")) {
        this.couponList = window.JSON.parse(
          window.JSON.stringify(this.originalCouponList)
        );
        next();
      } else {
        this.$bus.$emit("setCurrentIndex010001To3");
        window.localStorage.setItem("currentIndex010001", 3);
        next(false);
      }
    } else {
      next();
    }
  },
  async mounted() {
    let data = {
      is_active: true,
      platform: 10003,
    };
    this.isLoading = true;
    try {
      const response = await $getProviderInfo(data);
      console.log("info", response);
      const { Code, msg } = response;
      // 获取当前登录用户所有工单数据
      if (Code === 200 && msg === "success") {
        console.log(response.details.notifygenerals);
        this.listAll = response.details.notifygenerals.reverse(); // 将数据赋值给listAll
        this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // 重新计算totalPage
        const tempListAll = window.JSON.parse(
          window.JSON.stringify(this.listAll)
        );
        // 若listShow数组为空，则说明是首次加载组件
        if (this.listShow.length === 0) {
          this.listShow = tempListAll.slice(0, this.pageSize);
        }
        // 若进来页面时分页的currentPage和分页的totalPage相等，因为listAll的数据有可能进行了更新，所以需要更新listShow
        if (this.currentPage === this.totalPage) {
          this.listShow = tempListAll.slice(
            this.pageSize * (this.currentPage - 1),
            this.pageSize * this.currentPage
          );
        }
      } else {
        this.$toast.error("获取工单数据失败 ", 240);
        console.log("获取工单数据失败 ");
      }
    } catch (err) {
      this.$toast.error("获取工单数据失败  ", 240);
      console.log("获取工单数据失败 ");
    }
    this.isLoading = false;
  },
  data() {
    return {
      ifShowAdd: false,

      listAll: [],
      listShow: [],
      pageSize: 8,
      currentPage: 1,
      totalPage: 0,
      isShowCoverPage: false,
      showIndex: -1,
      x: null,
      y: null,
      isLoading: false,
    };
  },
  methods: {
    showAddDialog() {
      this.ifShowAdd = true;
    },
    hideAddDialog() {
      this.ifShowAdd = false;
    },

    beforeUnloadHandler(e) {
      if (this.updateMark) {
        e.returnValue = "离开此页面?";
      } else {
        return;
      }
    },
  },
  watch: {
    currentPage(newVal) {
      const tempListAll = window.JSON.parse(
        window.JSON.stringify(this.listAll)
      );
      this.listShow = tempListAll.slice(
        this.pageSize * (newVal - 1),
        this.pageSize * newVal
      );
    },
  },
  components: {
    Scroll,
    AddInfo,
    DeleteEnsureDialog2,
    EditReasonDialog,
    EmptyDataPlaceholder,
    OrderList,
  },
};
</script>

